<template>
  <v-autocomplete
    v-model="model"
    :items="items"
    :placeholder="placeholder"
    :prepend-inner-icon="prependInnerIcon"
    @change="onChange"
  />
</template>

<script>
export default {
  props: {
    id: { default: null }, 
    items: { default: () => [] },
    placeholder: { default: null },
    prependInnerIcon: { default: null },
    value: { default: null }
  },
  data: () => ({
    model: null
  }),
  watch: {
    value (val) {
      this.model = val
    }
  },
  mounted () {
    this.model = this.value
  },
  methods: {
    onChange (value) {
      this.$emit('onChange', value, this.id)
    }
  }
}
</script>