<template>
  <div class="choose-services-types">
    <checkbox-field
      v-for="serviceType in serviceTypesCmpt"
      :id="serviceType.id"
      :isChecked="serviceType.isChecked"
      :key="serviceType.id"
      :label="serviceType.name"
      @onChange="onChangeCheckbox" 
    />
  </div>
</template>

<script>
import CheckboxField from  '@/components/globais/form/CheckboxField'
import ChoosedServiceTypesMixin from '@/mixins/ChoosedServiceTypesMixin'
import UserMixin from '@/mixins/UserMixin'
export default {
  components: { CheckboxField },
  mixins: [ ChoosedServiceTypesMixin, UserMixin ],
  data: () => ({
    serviceTypes: []
  }),
  props: {
    choosedServiceTypes: { default: () => [] }
  },
  computed: {
    serviceTypesCmpt () {
      const choosedServiceTypes = [... this.choosedServiceTypes]
      return this.serviceTypes.map(serviceType => {
        serviceType.isChecked = choosedServiceTypes.some(serviceTypeId => Number(serviceTypeId) === Number(serviceType.id))
        return serviceType
      })
    }
  },
  mounted () {
    this.getServiceTypes()
  },
  methods: {
    getServiceTypes () {
      this.$axios(`${this.$url_api}/service-types`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.onGetServiceTypesWithSuccess(response.data.corpo)
          }
        }
      })
    },
    onChangeCheckbox (value, id) {
      const serviceTypes = [... this.serviceTypes]
      const index = serviceTypes.findIndex(serviceType => serviceType.id === id)
      if (index > -1) serviceTypes[index].isChecked = value
      this.setChoosedServiceTypes(serviceTypes)
    },
    onGetServiceTypesWithSuccess (serviceTypes) {
      this.serviceTypes = serviceTypes.map(serviceType => {
        serviceType.isChecked = false
        return serviceType
      })
    },
    setChoosedServiceTypes (serviceTypes) {
      const choosedServiceTypes = serviceTypes.filter(serviceType => serviceType.isChecked).map(serviceType => serviceType.id)
      this.$emit('onChange', choosedServiceTypes)
      //this.setChoosedServiceTypesState(choosedServiceTypes)
    }
  }
}
</script>