export default {
  computed: {
    choosedServiceTypesState() {
      return this.$store.getters['ChoosedServiceTypesStore/choosedSeviceTypesState'] || []
    }
  },
  methods: {
    setChoosedServiceTypesState(serviceTypes) {
      this.$store.dispatch('ChoosedServiceTypesStore/setChoosedSeviceTypes', serviceTypes)
    }
  }
}