<template>
  <div class="filters">
    <filter-item class="pt-0" title="Escolha o local">
      <choose-city
        :city="choosedCity"
        @onChange="changeChoosedCity" 
      />
    </filter-item>
    <v-divider></v-divider>
    <filter-item title="Quando deseja agendar">
      <choose-date @onChange="changeChoosedDate" />
    </filter-item>
    <v-divider></v-divider>
    <filter-item title="Preço">
      <choose-price
        :minPrice="minPrice"
        :maxPrice="maxPrice"
        @onChange="changePrice" 
      />
    </filter-item>
    <v-divider></v-divider>
    <filter-item class="pb-0" title="Serviços">
      <choose-service-types
        :choosedServiceTypes="choosedServiceTypes"
        @onChange="changeChoosedServiceTypes" 
      />
    </filter-item>
    <div class="buttons mt-8 mb-4">
      <btn-filter
        class="btn-filter"
        text="Filtrar" 
        :width="120"
        @click="filter"
      />
      <btn-cancel
        class="ml-md-2 mt-4 mt-md-0"
        :height="48" 
        @click="cancel"
      />
    </div>
  </div>
</template>

<script>
import BtnCancel from '@/components/site/buttons/BtnCancel'
import BtnFilter from '@/components/site/buttons/BtnFilter'
import ChooseCity from './ChooseCity'
import ChooseDate from './ChooseDate'
import ChoosePrice from './ChoosePrice'
import ChooseServiceTypes from './ChooseServiceTypes'
import FilterItem from './FilterItem'
import ChoosedBarbershopCityMixin from '@/mixins/ChoosedBarbershopCityMixin'
import ChoosedBarbershopDateMixin from '@/mixins/ChoosedBarbershopDateMixin'
import ChoosedBarbershopPriceRangeMixin from '@/mixins/ChoosedBarbershopPriceRangeMixin'
import ChoosedServiceTypesMixin from '@/mixins/ChoosedServiceTypesMixin'
import DateMixin from '@/mixins/DateMixin'
export default {
  components: {
    BtnCancel, 
    BtnFilter,
    ChooseCity, 
    ChooseDate, 
    ChooseServiceTypes, 
    ChoosePrice, 
    FilterItem 
  },
  mixins: [ ChoosedBarbershopCityMixin, ChoosedBarbershopDateMixin, ChoosedBarbershopPriceRangeMixin, ChoosedServiceTypesMixin, DateMixin ],
  data: () => ({
    choosedCity: null,
    choosedDate: null,
    choosedServiceTypes: [],
    minPrice: null,
    maxPrice: null
  }),
  watch: {
    choosedBarbershopPriceRangeState (priceRange) {
      if (priceRange) {
        this.changePrice(priceRange.minPrice, priceRange.maxPrice)
      } else {
        this.changePrice(null, null)
      }
    },
    choosedServiceTypesState (serviceTypes) {
      this.choosedServiceTypes = serviceTypes
    }
  },
  mounted () {
    this.setDataFromStore()
  },
  methods: {
    cancel () {
      this.setDataFromStore()
    },
    changeChoosedCity (city) {
      this.choosedCity = city
    },
    changeChoosedDate (date) {
      this.choosedDate = date
    },
    changeChoosedServiceTypes (serviceTypes) {
      this.choosedServiceTypes = serviceTypes
    },
    changePrice (minPrice, maxPrice) {
      this.minPrice = minPrice
      this.maxPrice = maxPrice
    },
    filter () {
      this.setChoosedBarbershopDateState(this.choosedDate)
      this.setChoosedBarbershopCityState(this.choosedCity)
      this.setChoosedServiceTypesState(this.choosedServiceTypes)
      this.setChoosedBarbershopPriceRangeState(this.minPrice, this.maxPrice)
      this.$emit('filter')
    },
    setDataFromStore () {
      const date = this.formatDateToDateEn(new Date())
      this.setChoosedBarbershopDateState(date)
      this.choosedCity = this.choosedBarbershopCityState
      this.choosedDate = date
      this.choosedServiceTypes = this.choosedServiceTypesState
      this.setPriceRange()
    },
    setPriceRange () {
      const priceRange = this.choosedBarbershopPriceRangeState
      window.console.log(priceRange)
      this.minPrice = priceRange ? priceRange.minPrice : null
      this.maxPrice = priceRange ? priceRange.maxPrice : null
    }
  }
}
</script>

<style lang="scss" scoped>
.buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media all and (max-width: 959px) {
  .buttons { flex-direction: column; 
    > .v-btn { width: 250px !important; }
  }
}
