<template>
  <div class="used-filters d-flex">
    <used-filters-item
      v-if="choosedBarbershopCityState"
      class="mr-2 mr-sm-4 mb-2 mb-sm-2" 
      :noIcon="true"
      :text="choosedBarbershopCityState"
    />
    <used-filters-item
      v-if="choosedBarbershopDateState"
      class="mr-2 mr-sm-4 mb-2 mb-sm-2"
      :noIcon="true"
      :text="dateCmpt" 
    />
    <used-filters-item
      v-if="choosedBarbershopPriceRangeState"
      :text="priceRangeCmpt" 
      class="mr-2 mr-sm-4 mb-2 mb-sm-2"
      @remove="resetPriceRange"
    />
    <template v-if="choosedServiceTypesCmpt.length > 0">
    <used-filters-item
      v-for="serviceType in choosedServiceTypesCmpt"
      :key="serviceType.id"
      :id="serviceType.id"
      :text="serviceType.id + ' - ' + serviceType.name" 
      class="mr-2 mr-sm-4 mb-2 mb-sm-2"
      @remove="removeServiceType"
    />
    </template>
  </div>
</template>

<script>
import UsedFiltersItem from './UsedFiltersItem'
import ChoosedBarbershopCityMixin from '@/mixins/ChoosedBarbershopCityMixin'
import ChoosedBarbershopDateMixin from '@/mixins/ChoosedBarbershopDateMixin'
import ChoosedBarbershopPriceRangeMixin from '@/mixins/ChoosedBarbershopPriceRangeMixin'
import ChoosedServiceTypesMixin from '@/mixins/ChoosedServiceTypesMixin'
import DateMixin from '@/mixins/DateMixin'
import TextMixin from '@/mixins/TextMixin'
export default {  
  components: { UsedFiltersItem },
  mixins: [
    ChoosedBarbershopCityMixin,
    ChoosedBarbershopDateMixin,
    ChoosedBarbershopPriceRangeMixin,
    ChoosedServiceTypesMixin,
    DateMixin,
    TextMixin
  ],
  data: () => ({
    serviceTypes: []
  }),
  computed: {
    dateCmpt () {
      return this.formatDateStringToDatePtBr(this.choosedBarbershopDateState)
    },
    priceRangeCmpt () {
      const priceRange = this.choosedBarbershopPriceRangeState
      const maxPrice = Math.ceil(Number(priceRange.maxPrice)).toFixed(2)
      const minPrice = Math.ceil(Number(priceRange.minPrice)).toFixed(2)
      return `R$ ${this.setMoneyMask(minPrice)} - R$ ${this.setMoneyMask(maxPrice)}`
    },
    choosedServiceTypesCmpt () {
      const choosedServiceTypes = [...this.choosedServiceTypesState]
      const serviceTypes = [... this.serviceTypes]
      return serviceTypes.map(serviceType => {
        serviceType.isChecked = choosedServiceTypes.some(serviceTypeId => Number(serviceTypeId) === Number(serviceType.id))
        return serviceType
      }).filter(serviceType => serviceType.isChecked)
    }
  },
  mounted () {
    this.getServiceTypes()
  },
  methods: {
    getServiceTypes () {
      this.$axios(`${this.$url_api}/service-types`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.serviceTypes = response.data.corpo.map(serviceType => {
              serviceType.isChecked = false
              return serviceType
            })
          }
        }
      })
    },
    removeServiceType (serviceTypeId) {
      let choosedServiceTypesId = [... this.choosedServiceTypesState]
      const index = choosedServiceTypesId.findIndex(id => Number(id) === Number(serviceTypeId))
      if (index > -1) choosedServiceTypesId.splice(index, 1)
      this.setChoosedServiceTypesState(choosedServiceTypesId)
      this.$emit('filter')
    },
    resetPriceRange () {
      this.resetChoosedBarbershopPriceRangeState()
      this.$emit('filter')
    }
  }
}
</script>

<style scoped>
.used-filters { flex-wrap: wrap; }
</style>