<template>
  <v-btn
    class="text-none primary border-radius-small"
    :height="height"
    :width="width"
    @click="click"
  >
    {{ text }}
  </v-btn>
</template>

<script>
export default {
  props: {
    height: { default: 48 },
    width: { default: 64 },
    text: { required: true }
  },
  methods: {
    click () {
      this.$emit('click')
    }
  }
}
</script>