export default {
  computed: {
    choosedBarbershopPriceRangeState() {
      return this.$store.getters['ChoosedBarbershopPriceRangeStore/priceRange']
    }
  },
  methods: {
    setChoosedBarbershopPriceRangeState(minPrice, maxPrice) {
      const priceRange = { minPrice, maxPrice }
      this.$store.dispatch('ChoosedBarbershopPriceRangeStore/setPriceRange', priceRange)
    },
    resetChoosedBarbershopPriceRangeState() {
      this.$store.dispatch('ChoosedBarbershopPriceRangeStore/setPriceRange', null)
    }
  }
}