<template>
  <v-range-slider
    v-if="min && max"
    hide-details
    v-model="model"
    :value="model"
    :max="maxCmpt"
    :min="minCmpt"
    :step="1"
    @end="change"
    @input="change"
  ></v-range-slider>
</template>

<script>
export default {
  props: {
    max: { default: 100 },
    min: { default: 0 },
    maxValue: { default: 100 },
    minValue: { default: 0 },
  },
  data: (vm) => ({
    model: [vm.min, vm.max],
  }),
  watch: {
    maxValue(value) {
      this.model[1] = value || this.max
    },
    minValue(value) {
      this.model[0] = value || this.min
    },
  },
  mounted() {
    /*const minValue = this.minValue || this.min
    const maxValue = this.maxValue || this.max
    this.model = [minValue, maxValue];*/
  },
  computed: {
    maxCmpt() {
      return Math.ceil(Number(this.max));
    },
    minCmpt() {
      return Math.floor(Number(this.min));
    },
  },
  methods: {
    change(priceRange) {
      this.$emit('onChange', priceRange);
    },
  },
};
</script>
