<template>
  <div class="container my-8">
    <app-title :text="pageTitle" class="page-title font-weight-regular" />
    <v-row class="mt-4">
      <v-col cols="auto" class="d-none d-md-flex">
        <aside class="side-bar">
          <v-card class="pa-4">
            <filters @filter="getBarbershopsServicesOnService" />
          </v-card>
        </aside>
      </v-col>
      <v-col class="ml-0 ml-sm-4">
        <v-row>
          <v-col class="py-0 px-0">
            <used-filters @filter="getBarbershopsServicesOnService" />
          </v-col>
          <v-col class="py-0 d-md-none" cols="auto">
            <filter-mobile-button @click="openFilterDialog" />
          </v-col>
          <v-col cols="12" class="px-0">
            <barbershops ref="barbershops" />
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <filter-dialog
      ref="filterDialog"
      @filter="getBarbershopsServicesOnService"
    />
  </div>
</template>

<script>
import AppTitle from '@/components/site/Title';
import Barbershops from '@/components/site/search/Barbershops'
import FilterDialog from '@/components/site/search/FilterDialog';
import FilterMobileButton from '@/components/site/search/FilterMobileButton';
import Filters from '@/components/site/search/Filters';
import UsedFilters from '@/components/site/search/UsedFilters';
import ChoosedBarbershopCityMixin from '@/mixins/ChoosedBarbershopCityMixin';
import ChoosedBarbershopDateMixin from '@/mixins/ChoosedBarbershopDateMixin';
import ChoosedBarbershopPriceRangeMixin from '@/mixins/ChoosedBarbershopPriceRangeMixin';
import ChoosedServiceTypesMixin from '@/mixins/ChoosedServiceTypesMixin';

import DateMixin from '@/mixins/DateMixin';
import GeolocationMixin from '@/mixins/GeolocationMixin'

export default {
  components: {
    AppTitle,
    Barbershops,
    FilterDialog,
    FilterMobileButton,
    Filters,
    UsedFilters,
  },
  mixins: [
    ChoosedBarbershopCityMixin,
    ChoosedBarbershopDateMixin,
    ChoosedBarbershopPriceRangeMixin,
    ChoosedServiceTypesMixin,
    DateMixin,
    GeolocationMixin
  ],
  data: () => ({
    pageTitle: 'Resultados',
    hasUserLocation: false
  }),
  created() {
    this.setPageTitle()
    this.$store.dispatch('ChoosedBarbershopCityStore/init');
    this.$store.dispatch('ChoosedServiceTypesStore/init');
    this.$store.dispatch('ChoosedBarbershopPriceRangeStore/init');
  },
  mounted () {
    this.getBarbershopsServicesOnService();
  },
  methods: {
    getBarbershopsServicesOnService() {
      this.$refs.barbershops.getBarbershops()
    },
    openFilterDialog() {
      this.$refs.filterDialog.openDialog();
    },
    setPageTitle () {
      const permission = this.hasGeolocationPermission()
      permission.then(p => {
        this.pageTitle = p ? 'Resultados Próximos a você' : 'Resultados'
      })
    }
  },
};
</script>

<style lang="scss" scoped>
@import '../../scss/theme.scss';
.side-bar { width: 300px; }
.v-application .page-title { color: $primary !important; }
</style>
