<template>
  <v-btn
    class="text-none"
    :height="height"
    text
    @click="click"
  >
    {{ text }}
  </v-btn>
</template>

<script>
export default {
  props: { 
    height: { default: null },
    text: { default: 'Cancelar' }
  },
  methods: {
    click () {
      this.$emit('click')
    }
  }
}
</script>