<template>
  <div class="choose-price">
    <div 
      v-if="minPriceModel || maxPriceModel"
      class="prices"
    >
      <span>R$ {{ minPriceCmpt | numeric }}</span>
      <span>R$ {{ maxPriceCmpt | numeric }}</span>
    </div>
    <range-slider 
      :max="max"
      :min="min"
      :maxValue="maxPriceModel"
      :minValue="minPriceModel"
      @onChange="changeRanger"
    />
    <div class="prices prices-small">
      <span>R$ {{ minCmpt | numeric }}</span>
      <span>R$ {{ maxCmpt | numeric }}</span>
    </div>
  </div>
</template>

<script>
import RangeSlider from '@/components/globais/form/RangeSlider'
import TextMixin from '@/mixins/TextMixin'
export default {
  components: { RangeSlider },
  mixins: [ TextMixin ],
  props: {
    minPrice: { default: 0 },
    maxPrice: { default: 100 }
  },
  data: () => ({
    max: 0,
    min: 100,
    minPriceModel: 0,
    maxPriceModel: 100
  }),
  computed: {
    maxCmpt () {
      return Math.ceil(Number(this.max)).toFixed(2)
    },
    minCmpt () {
      return Math.floor(Number(this.min)).toFixed(2)
    },
    minPriceCmpt () {
      return Math.ceil(Number(this.minPriceModel)).toFixed(2)
    },
    maxPriceCmpt () {
      return Math.ceil(Number(this.maxPriceModel)).toFixed(2)
    }
  },
  watch: {
    minPrice (value) {
      this.minPriceModel = value
    },
    maxPrice (value) {
      this.maxPriceModel = value
    }
  },
  mounted () {
    this.minPriceModel = this.minPrice
    this.maxPriceModel = this.maxPrice
    this.getPricesRangerOnService()
  },
  methods: {
    changeRanger (priceRange) {
      let [minPrice, maxPrice] = [... priceRange]
      this.minPriceModel = minPrice
      this.maxPriceModel = maxPrice
      this.$emit('onChange', minPrice, maxPrice)
    },
    getPricesRangerOnService () {
      this.$axios(`${this.$url_api}/service/price-ranger`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.min = response.data.corpo.min_price
            this.max = response.data.corpo.max_price
          }
        }
      }).finally(() => {
        this.minPriceModel = this.minPrice
        this.maxPriceModel = this.maxPrice
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.prices {
  display: flex;
  font-size: 1rem;
  justify-content: space-between;
  font-weight: 500;
  &.prices-small { font-size: .8rem; }
}
</style>