<template>
  <div class="choose-location">
    <autocomplete-field 
      :items="citiesCmpt"
      placeholder="Informe o nome da cidade"
      prependInnerIcon="place"
      :value="cityModel"
      @onChange="setCity"
    />
  </div>
</template>

<script>
import AutocompleteField from '@/components/globais/form/AutocompleteField'
import nominatim from 'nominatim-client'
export default {
  components: { AutocompleteField },
  props: {
    city: { default: null }
  },
  data: () => ({
    cities: [],
    cityModel: null
  }),
  watch: {
    city (value) {
      this.cityModel = value
    }
  },
  computed: {
    citiesCmpt () {
      return [...new Set(this.cities)]
    }
  },
  mounted () {
    this.cityModel = this.city
    this.getBarbershopsCitiesOnAPI()
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(position => this.getCityOnAPI(position))
    }
  },
  methods: {
    getBarbershopsCitiesOnAPI () {
      this.$axios(`${this.$url_api}/barbershop/cities`)
      .then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            const cities = response.data.corpo.map(city => city.name)
            this.cities.push(...cities)
          }
        }
      })
    },
    getCityOnAPI (position) {
      const query = {
        lat: position.coords.latitude,
        lon: position.coords.longitude
      }
      const client = nominatim.createClient({
        useragent: "Meu Barbeiro",
        referer: 'https://meubarbeiro.app'
      })
      client.reverse(query).then(response => {
        this.setCityOnAPIByLocation(response)
      })
    },
    setCity (city) {
      this.$emit('onChange', city)
    },
    setCityOnAPIByLocation (data) {
      this.cities.push(data.address.city)  
      this.cityModel = data.address.city
      this.setCity(this.cityModel)
    }
  }
}
</script>