<template>
  <v-badge
    color="success"
    :content="qtdUsedFiltersCmpt"
    :value="qtdUsedFiltersCmpt"
    offset-x="5"
    offset-y="10"
    :size="10"
  >
    <v-btn
      :min-width="0" 
      text
      class="button"
      @click="click"
    >
      <v-icon>filter_list</v-icon>
    </v-btn>
  </v-badge>
</template>

<script>
import ChoosedBarbershopCityMixin from '@/mixins/ChoosedBarbershopCityMixin'
import ChoosedBarbershopDateMixin from '@/mixins/ChoosedBarbershopDateMixin'
import ChoosedBarbershopPriceRangeMixin from '@/mixins/ChoosedBarbershopPriceRangeMixin'
import ChoosedServiceTypesMixin from '@/mixins/ChoosedServiceTypesMixin'
export default {
  mixins: [
    ChoosedBarbershopCityMixin,
    ChoosedBarbershopDateMixin,
    ChoosedBarbershopPriceRangeMixin,
    ChoosedServiceTypesMixin
  ],
  computed: {
    qtdUsedFiltersCmpt () {
      let filterQuantity = 0
      if (this.choosedBarbershopCityState) filterQuantity++
      if (this.choosedBarbershopDateState) filterQuantity++
      if (this.choosedBarbershopPriceRangeState) filterQuantity++
      filterQuantity += this.choosedServiceTypesState.length
      return filterQuantity
    }
  },
  methods: {
    click () {
      this.$emit('click')
    }
  }
}
</script>

<style scoped>
.button { padding: 0 !important; }
</style>