<template>
  <v-row class="pa-2">
    <v-col class="py-0">
      <h1 class="name primaryLight--text">
        {{ service.name }}
      </h1>
      <p v-if="service.description" class="description primaryLight--text ma-0">
        {{ service.description }}
      </p>
    </v-col>
    <v-col cols="auto" class="py-0 text-right">
      <span class="d-block price">
        {{ price }}
      </span>
      <span class="d-block time">
        {{ durationTime | timeToText }}
      </span>
    </v-col>
    <v-col cols="auto" class="py-0">
      <v-btn
        class="btn-reservar text-none success elevation-0 primaryLight--text px-4 py-1"
        dark
        @click="resgatar"
      >
        Agendar
      </v-btn>
    </v-col>
  </v-row>
</template>

<script>
import TextMixin from '@/mixins/TextMixin'
import TimeMixin from '@/mixins/TimeMixin'
export default {
  mixins: [ TextMixin, TimeMixin ],
  props: {
    service: { required: true }
  },
  computed: {
    durationTime () {
      return this.service.duration_time
    },
    price () {
      return `R$ ${ this.setMoneyMask(this.service.price) }`
    }
  },
  methods: {
    resgatar () {
      this.$emit('toSchedule', this.service.id)
    }
  }
}
</script>

<style>
.name { margin: 0; font-size: 1rem; font-weight: 500; }
.description { margin: 0; font-size: .8rem; }
.price { font-weight: bold; font-size: .8rem; }
.time { font-size: .8rem; }
.btn-reservar { border-radius: 45px; color: #fff; font-size: .8rem !important; }
@media all and (max-width: 599px) {
  .name { font-size: .8rem; }  
  .btn-reservar { height: 40px; font-size: .7rem !important; }
}
</style>