<template>
  <div class="barbershops">
    <barbershop-item 
      v-for="barbershop in barbershops"
      :key="barbershop.id"
      :barbershop="barbershop"
    />
    <p v-if="barbershops.length === 0" class="mt-8 text-center">
      Nenhuma barbearia encontrada
    </p>
    <loader ref="loader" />
  </div>
</template>

<script>
import BarbershopItem from './BarbershopItem'
import Loader from '@/components/site/Loader'

import ChoosedBarbershopCityMixin from '@/mixins/ChoosedBarbershopCityMixin';
import ChoosedBarbershopDateMixin from '@/mixins/ChoosedBarbershopDateMixin';
import ChoosedBarbershopPriceRangeMixin from '@/mixins/ChoosedBarbershopPriceRangeMixin';
import ChoosedServiceTypesMixin from '@/mixins/ChoosedServiceTypesMixin';

export default {
  components: { BarbershopItem, Loader },
  mixins: [ ChoosedBarbershopCityMixin, ChoosedBarbershopDateMixin, ChoosedBarbershopPriceRangeMixin, ChoosedServiceTypesMixin ],
  data: () => ({
    barbershops: []
  }),
  methods: {
    getBarbershops () {
      this.$refs.loader.show()
      let url = `${this.$url_api}/barbershop/find?date=${this.choosedBarbershopDateState}`
      if (this.choosedBarbershopCityState) {
        url += `&city=${this.choosedBarbershopCityState}`
      }
      if (this.choosedServiceTypesState.length > 0) {
        url += `&services=${this.choosedServiceTypesState.join(',')}`
      }
      const priceRange = this.choosedBarbershopPriceRangeState
      if (priceRange) {
        url += `&minPrice=${priceRange.minPrice}&maxPrice=${priceRange.maxPrice}`
      }      
      this.$axios(url).then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.barbershops = response.data.corpo
          }
        }
      }).finally(() => this.$refs.loader.hide())
    }
  }
}
</script>