<template>
  <div class="barbershop-item">
    <v-row>
      <v-col cols="12" sm="auto">
        <div class="image-box elevation-1 text-center">
          <router-link :to="{ path: `barbearia/${barbershop.id}/${this.formatTextToUrl(barbershop.name)}` }">
            <img v-if="image" :src="image" class="image" />
          </router-link>
        </div>
      </v-col>
      <v-col class="ml-0 ml-sm-2">
        <header class="mb-2">
          <router-link :to="{ path: `barbearia/${barbershop.id}/${this.formatTextToUrl(barbershop.name)}` }">
            <h1 class="name primaryLight--text">
              {{ barbershop.name }}
            </h1>
          </router-link>
          <p v-if="phoneNumber" class="phone-address primaryLight--text ma-0">
            <v-icon color="primary" class="mr-1" small>phone</v-icon> {{ phoneNumber }}                 
          </p>
          <p v-if="address" class="phone-address primaryLight--text ma-0">
            <v-icon color="primary" class="mr-1" small>place</v-icon> {{ address }}    
          </p>
        </header>
        <main v-if="services.length > 0" class="services">
          <v-row>
            <v-col
              v-for="service in services"
              :key="service.id"
              cols="12"
              class="service py-0"
            >
              <v-divider></v-divider>
              <barbershop-service
                :service="service"
                @toSchedule="schedule"                
              />
            </v-col>
          </v-row>
        </main>
      </v-col>
    </v-row>
    <v-divider class="my-4"></v-divider>
    <schedule-dialog ref="scheduleDialog" />
  </div>
</template>

<script>
import BarbershopService from './BarbershopService'
import ScheduleDialog from '@/components/site/schedule/ScheduleDialog'

import NavigationMixin from '@/mixins/NavigationMixin'
import TextMixin from '@/mixins/TextMixin'
import UserMixin from '@/mixins/UserMixin' 

export default {
  components: { BarbershopService, ScheduleDialog },
  mixins: [ NavigationMixin, TextMixin, UserMixin ],
  props: {
    barbershop: { required: true }
  },
  computed: {
    address () {
      if (this.barbershop.address) {
        const address = this.barbershop.address
        let formattedAddress = `${address.public_place}, ${address.number},`
        if (address.complement) formattedAddress += ` ${address.complement},`
        formattedAddress += ` ${address.neighborhood} - ${address.city}`
        return formattedAddress
      }
      return null
    },
    image () {
      if (this.barbershop.image_url) 
        return `${this.$url_backend}${this.barbershop.image_url}`

      return null
    },
    phoneNumber () {
      if (this.barbershop.phone_number)
        return this.setPhoneMask(this.barbershop.phone_number)
      
      return null
    },
    services () {
      const barbershopServices = [...this.barbershop.services]
      return barbershopServices //barbershopServices.splice(0, 3)
    }
  },
  methods: {
    schedule (serviceId) {
      if (this.isUserAutenticated()) {
        this.$refs.scheduleDialog.showDialog(this.barbershop.id, serviceId, this.userState.id)
      } else this.goTo('login') 
    }
  }
}
</script>

<style scoped>
.image-box { width: 200px; height: 200px; border-radius: 5px; overflow: hidden; }
.imagem { width: 100%; }
.name { font-size: 1.8rem; margin: 0; font-weight: 500; }
a { text-decoration: none; }
a:hover { text-decoration: revert; }
.phone-address {
  display: flex;
  align-content: center;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: .9rem;
}
.service { border-radius: 5px; }
.services { max-height: 165px; overflow: hidden; overflow-y: auto; }
@media all and (max-width: 599px) {
  .image-box { margin: auto; }
  header, main { max-width: 420px; margin: auto; }
  .name { font-size: 1.5rem; }
  .phone-address { font-size: .8rem; }
}
</style>