<template>
  <v-card class="used-filters-item d-flex d-inline-block px-2">
    {{ text }}
    <v-btn v-if="!noIcon" text class="pa-0 ml-4" :minWidth="0" :height="0" @click="remove">
      <v-icon>highlight_off</v-icon>
    </v-btn>
  </v-card>
</template>

<script>
export default {
  props: {
    id: { default: null },
    noIcon: { default: false },
    text: { required: true }
  },
  methods: {
    remove () {
      this.$emit('remove', this.id)
    }
  }
}
</script>

