<template>
  <div class="choose-date">
    <date-picker
      cssClass="pt-0"
      label="" 
      :value="date" 
      @change="changeDate"
    />
  </div>
</template>

<script>
import DatePicker from '@/components/globais/form/date-picker/DatePicker'
import ChoosedBarbershopDateMixin from '@/mixins/ChoosedBarbershopDateMixin'
export default {
  components: { DatePicker },
  mixins: [ ChoosedBarbershopDateMixin ],
  props: {
    date: { default: null }
  },
  methods: {
    changeDate (date) {
      this.$emit('onChange', date)
    }
  }
}
</script>

<style>
.choose-date .v-input { padding-top: 0; margin-top: 0; }
</style>