<template>
  <v-container
    class="px-0"
    fluid
  >
    <v-checkbox
      v-model="checked"
      :label="label"
      :hide-details="true"
      class="mt-0 checkbox"
    ></v-checkbox>
  </v-container>
</template>

<script>
export default {
  props: {
    id: { default: null },
    isChecked: { default: false },
    label: { default: null }
  },
  data: () => ({
    checked: false
  }),
  watch: {
    isChecked (value) {
      this.checked = value
    },
    checked (value) {
      this.$emit('onChange', value, this.id)
    }
  },
  mounted () {
    this.checked = this.isChecked
  } 
}
</script>

<style>
.v-aplication .checkbox { margin-top: 0 !important; }
</style>